.taroify-pull-refresh {
  overflow: hidden;
  user-select: none;
}
.taroify-pull-refresh__track {
  position: relative;
  height: 100%;
  transition-property: transform;
}
.taroify-pull-refresh__head {
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--pull-refresh-head-height, 100px);
  overflow: hidden;
  font-size: var(--pull-refresh-head-font-size, var(--font-size-md, 28px));
  line-height: var(--pull-refresh-head-line-height, var(--pull-refresh-head-height, 100px));
  color: var(--pull-refresh-head-color, var(--gray-6, #969799));
  text-align: center;
  transform: translateY(-100%);
}
.taroify-pull-refresh__loading .taroify-loading__circular,
.taroify-pull-refresh__loading .taroify-loading__spinner {
  width: var(--pull-refresh-loading-icon-width, var(--pull-refresh-loading-icon-size, 32px));
  height: var(--pull-refresh-loading-icon-height, var(--pull-refresh-loading-icon-size, 32px));
}