.taroify-tabs__tab {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  min-width: 0;
  padding: var(--tab-padding, 0 var(--padding-base, 8px));
  font-size: var(--tab-font-size, var(--font-size-md, 28px));
  line-height: var(--tab-line-height, var(--line-height-md, 40px));
  color: var(--tab-color, var(--gray-7, #646566));
  cursor: pointer;
}
.taroify-tabs__tab--active {
  font-weight: var(--tab-active-font-weight, var(--font-weight-bold, 500));
  color: var(--tab-active-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-tabs__tab--disabled {
  color: var(--tab-disabled-color, var(--gray-5, #c8c9cc));
  cursor: not-allowed;
}
.taroify-tabs__tab__content {
  display: flex;
  align-items: center;
  justify-content: center;
}
.taroify-tabs__tab__content-ellipsis {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.taroify-tabs {
  position: relative;
}
.taroify-tabs__track {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  will-change: left;
}
.taroify-tabs__content--animated {
  overflow: hidden;
}
.taroify-tabs__content .taroify-tabs__tab-pane, .taroify-tabs__content .taroify-tabs__tab-pane-wrapper {
  box-sizing: border-box;
  flex-shrink: 0;
  width: 100%;
}
.taroify-tabs__content .taroify-tabs__tab-pane-wrapper--inactive {
  height: 0;
  overflow: visible;
}

.taroify-tabs--line .taroify-tabs__wrap {
  height: var(--tabs-wrap-height, 88px);
}

.taroify-tabs__wrap {
  display: flex;
  overflow: hidden;
}
.taroify-tabs__wrap--page-top {
  position: fixed;
}
.taroify-tabs__wrap--content-bottom {
  top: auto;
  bottom: 0;
}
.taroify-tabs__wrap__scroll--line {
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  background: #fff;
}
.taroify-tabs__wrap__scroll--card {
  box-sizing: border-box;
  width: 100%;
  height: var(--tabs-card-height, 60px);
  margin: var(--tabs-card-margin, 0 var(--padding-md, 32px));
  border: var(--tabs-card-border-width, var(--border-width-base, 2px)) solid var(--tabs-card-border-color, var(--tabs-active-color, var(--primary-color, #1989fa)));
  border-radius: var(--tabs-card-border-radius, var(--border-radius-sm, 4px));
}
.taroify-tabs__wrap__scroll::-webkit-scrollbar {
  display: none;
}
.taroify-tabs__wrap--scrollable .taroify-tabs__tab {
  flex: 1 0 auto;
  padding: 0 var(--padding-sm, 24px);
}

.taroify-tabs__nav {
  position: relative;
  display: flex;
  user-select: none;
  background: var(--tabs-nav-background-color, var(--white, #fff));
}
.taroify-tabs__nav--line {
  box-sizing: content-box;
  height: 100%;
  padding-bottom: 30px;
  /* 15px padding to hide scrollbar in mobile safari */
}
.taroify-tabs__nav--card {
  box-sizing: content-box;
  height: 100%;
}
.taroify-tabs__nav--card .taroify-tabs__tab {
  color: var(--tabs-active-color, var(--primary-color, #1989fa));
  border-right: var(--border-width-base, 2px) solid var(--tabs-active-color, var(--primary-color, #1989fa));
}
.taroify-tabs__nav--card .taroify-tabs__tab:last-child {
  border-right: 0 none;
}
.taroify-tabs__nav--card .taroify-tabs__tab--active {
  color: var(--white, #fff);
  background: var(--tabs-active-color, var(--primary-color, #1989fa));
}
.taroify-tabs__nav--card .taroify-tabs__tab--disabled {
  color: var(--tab-disabled-color, var(--gray-5, #c8c9cc));
}

.taroify-tabs__line {
  position: absolute;
  bottom: 0;
  z-index: 1;
  width: var(--tabs-line-width, 80px);
  height: var(--tabs-line-height, 6px);
  background: var(--tabs-line-background-color, var(--tabs-active-color, var(--primary-color, #1989fa)));
  border-radius: var(--tabs-line-border-radius, var(--tabs-line-height, 6px));
  transform: scale(0);
}
.taroify-tabs__line--active {
  transform: scale(1);
}