.taroify-swiper {
  position: relative;
  overflow: hidden;
  cursor: grab;
  user-select: none;
  transform: translateZ(0);
}
.taroify-swiper__track {
  display: flex;
  height: 100%;
}
.taroify-swiper__track--vertical {
  flex-direction: column;
}
.taroify-swiper__indicators {
  position: absolute;
  bottom: var(--swiper-indicators-margin, var(--padding-sm, 24px));
  left: 50%;
  display: flex;
  transform: translateX(-50%);
}
.taroify-swiper__indicators--vertical {
  top: 50%;
  bottom: auto;
  left: var(--swiper-indicators-margin, var(--padding-sm, 24px));
  flex-direction: column;
  transform: translateY(-50%);
}
.taroify-swiper__indicators--vertical .taroify-swiper__indicator:not(:last-child) {
  margin-bottom: var(--swiper-indicator-size, 12px);
}
.taroify-swiper__indicator {
  width: var(--swiper-indicator-width, var(--swiper-indicator-size, 12px));
  height: var(--swiper-indicator-height, var(--swiper-indicator-size, 12px));
  background: var(--swiper-indicator-background-color, var(--border-color, var(--gray-3, #ebedf0)));
  border-radius: 100%;
  opacity: var(--swiper-indicator-opacity, 0.3);
  transition: opacity var(--swiper-indicator-transition-duration, var(--animation-duration-fast, 0.2s)), background-color var(--swiper-indicator-transition-duration, var(--animation-duration-fast, 0.2s));
}
.taroify-swiper__indicator:not(:last-child) {
  margin-right: var(--swiper-indicator-size, 12px);
}
.taroify-swiper__indicator--active {
  background: var(--swiper-indicator-active-background-color, var(--primary-color, #1989fa));
  opacity: var(--swiper-indicator-active-opacity, 1);
}

.taroify-swiper-item {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
}